@charset "utf-8";
@use "mixin" as m;
// cmn

.wrap {
  background: #fff url("../img/bg_mv.png") no-repeat left top / 100% auto;
}

.sec {
  margin: 0 0 10rem;
  &__inner {
    max-width: 152rem;
    margin: auto;
    padding: 0 4rem;
  }
  &__ttlBox {
    margin: 0 0 6rem;
  }
  &__ttl {
    font-size: 6vw;
    padding: 0 0 0 4vw;
    margin: 0 0 0 -4vw;
    font-weight: bold;
    position: relative;
    line-height: 1;
    font-family: "Lato";
    letter-spacing: 3px;
    background: no-repeat url("../img/bg_ttl01.png") left center / auto 60%;
    @include m.mq(sp) {
      font-size: 12rem;
      padding: 0 0 0 7vw;
      margin: 0 0 0 0vw;
      background-size: auto 36%;
    }
    &-sub {
      font-size: 2.5rem;
    }
    &--small {
      font-size: 4vw;
      @include m.mq(sp) {
        font-size: 8rem;
      }
    }
    &--contact {
      background-image: url("../img/bg_ttl02.png");
    }
  }
}

.sec.about {
  position: relative;
  &::before {
    background: #f4f4f4;
    content: "";
    display: block;
    height: 60%;
    right: 0;
    position: absolute;
    bottom: 0;
    width: 70%;
    @include m.mq(sp) {
      width: 80%;
      height: 70%;
    }
  }
}

.sec.skill {
  position: relative;
  z-index: 1;
  &::before {
    z-index: -1;
    content: "";
    display: block;
    position: absolute;
    background-color: #eee;
    transform: rotate(-45deg);
    top: 12vw;
    width: 50vw;
    height: 50vw;
    right: calc(100% - 25vw);
    @include m.mq(sp) {
      top: 25vw;
      width: 100vw;
      height: 100vw;
      right: calc(100% - 50vw);
    }
  }
  &::after {
    z-index: -1;
    content: "";
    display: block;
    position: absolute;
    transform: rotate(-45deg);
    top: 12vw;
    width: 50vw;
    height: 50vw;
    background-color: #fff;
    right: calc(100% + 5vw);
    @include m.mq(sp) {
      top: 25vw;
      width: 100vw;
      height: 100vw;
      right: calc(100% + 15vw);
    }
  }
}

.sec.blog {
  position: relative;
  z-index: 1;
}

.sec.contact {
