//topのカテゴリ
.catsList {
  $this: &;
  &__item {
    display: inline-block;
    vertical-align: top;
    font-size: 1.8rem;
    padding: 0.5rem 1rem;
    color: #fff;
    background: #0e7dc7;
    margin: 0 0.5rem 1rem 0;
    @include m.mq(sp) {
      font-size: 1.7rem;
    }
  }
  &--single {
    margin-bottom: 2rem;
    #{$this}__item {
      font-size: 1.7rem;
    }
  }
}

// worksのcat
.catsList.catsList--works {
  display: inline-block;
  vertical-align: top;
  font-size: 1.8rem;
  padding: 0.5rem 1rem;
  color: #fff;
  background: #0e7dc7;
  margin: 0 0.5rem 1rem 0;
  @include m.mq(sp) {
    font-size: 1.7rem;
  }
}

//singleのcat
.post-categories {
  li {
    display: inline-block;
    vertical-align: top;
    font-size: 1.6rem;
    padding: 0.5rem 1rem;
    color: #fff;
    background: #0e7dc7;
    margin: 0 0.5rem 0rem 0;
    @include m.mq(sp) {
      font-size: 1.7rem;
    }
  }
}


/*デフォルトのマウスカーソルを非表示にする*/
// html,body,a{cursor: none;}

/*独自のマウスカーソルを作成*/
.cursor {
  position: fixed;
  top: -5px;
  left: -5px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.7);
  z-index: 4;
  transition: width 0.2s, height 0.2s, top 0.2s, left 0.2s;
  transition-timing-function: ease;
  transform: translate(0, 0);
  pointer-events: none;
}

/*aタグにホバーした時に見た目変化*/
.cursor.cursor--hover {
  top: -20px;
  left: -20px;
  width: 60px;
  height: 60px;
  background: rgba(15, 125, 200, 0.7);
}


.content {
  display: flex;
  justify-content: space-between;
  max-width: 152rem;
  margin: auto;
  @include m.mq(sp) {
    display: block;
  }
  &__main {
    width: 108rem;
    padding: 0rem 3rem 5rem;
    @include m.mq(sp) {
      width: 100%;
      padding: 0rem 0rem 3rem;
    }
  }
  &__side {
    width: 42rem;
    padding: 0 1rem 5rem;
    @include m.mq(sp) {
      width: 100%;
      padding: 0 0rem 3rem;
    }
  }
}
